@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
   font-family: 'Poppins';
}

body{
   background-color: #f9f9f9;
   padding: 0% 5%;
}


/* CSS del contador */

.Contador {
  text-align: center;
}



.title{
   color:red;
}

.numero-bajo {
  color: #dfce24;
}

.numero-correcto {
  color: green;
}

.numero-alto {
  color: red;
}

.cambiadorDeNumeros{
   padding:5px;
   margin: 20px;
   width: 150px;
   height: 50px;
   border: 1px solid #ddd;
   border-radius: 5px;
}

.descripcionEjercicio{
   text-align: left;
}

/* CSS de Lista de Alumnos */

h1{
   text-align: center;
}

li{
   list-style: none;
   width: 19%;
   display: inline-block;
   border: solid 1px #ddd;
   border-radius: 10px;
   box-sizing: border-box;
   padding: 0 10px;
   margin: 10px 5px;
}

li:hover{
   -webkit-animation: selectLi 1s linear;
           animation: selectLi 1s linear;
}

.botonAlumnos{
   padding: 5px 10px;
   margin: 0px 10px ;
}

@-webkit-keyframes selectLi{
   10%{
      background-color: #dddd;
      padding: 0px 10px;
      box-sizing: border-box;
      font-size: 16px;
   }
   50%{
      background-color: #dddd;
      padding: 0px 9px;
      box-sizing: border-box;
      font-size: 17px;
   }
   100%{
      background-color: #dddd;
      padding: 0px 10px;
      box-sizing: border-box;
      font-size: 16px; 
   }
}

@keyframes selectLi{
   10%{
      background-color: #dddd;
      padding: 0px 10px;
      box-sizing: border-box;
      font-size: 16px;
   }
   50%{
      background-color: #dddd;
      padding: 0px 9px;
      box-sizing: border-box;
      font-size: 17px;
   }
   100%{
      background-color: #dddd;
      padding: 0px 10px;
      box-sizing: border-box;
      font-size: 16px; 
   }
}
